/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable no-alert */
import React from 'react';
import { formatDistance } from 'date-fns';
import { StorageService } from '../../_data-layer/js/Services/StorageService/StorageService';
import { StoreProvider, useSearchHistory } from './store';

function HistorySection({ isReady, data, type, clearHistory }) {
    if (!data.length) {
        return (
            <p className="search-history__list-empty">
                Your {type} history is empty.
            </p>
        );
    }
    return (
        <>
            <ul className="search-history__list">
                {isReady &&
                    data.map((historyElement, id) => {
                        const { title, url, time, query, count } =
                            historyElement;

                        const friendlyDate = formatDistance(
                            new Date(),
                            new Date(time)
                        );
                        const friendlyName =
                            type === 'click' ? title : `${title} (${count})`;

                        return (
                            // eslint-disable-next-line react/no-array-index-key
                            <li className="search-history__list-item" key={id}>
                                <a href={url}>{friendlyName}</a> {friendlyDate}
                                {query && (
                                    <span> for &quot;{query}&quot; </span>
                                )}
                            </li>
                        );
                    })}
            </ul>
            <button
                type="button"
                className="search-history__button-clear"
                title="Clear history"
                onClick={() => {
                    // eslint-disable-next-line no-restricted-globals
                    if (confirm('Your search history will be cleared')) {
                        clearHistory(type);
                    }
                }}
            >
                Clear
            </button>
        </>
    );
}

function History(props) {
    const {
        clickHistoryLabel,
        searchHistoryLabel,
        showClickHistory,
        showSearchHistory,
    } = props;
    const { isReady, searchHistory, clickHistory, clearHistory } =
        useSearchHistory();

    return (
        <div className="search-history no-wysiwyg">
            {showClickHistory && (
                <section className="search-history__container">
                    <h2 className="search-history__heading">
                        <svg className="svg-icon">
                            <title>{clickHistoryLabel}</title>
                            <use href="#favorite" />
                        </svg>
                        {clickHistoryLabel}
                    </h2>
                    <HistorySection
                        type="click"
                        isReady={isReady}
                        data={clickHistory}
                        clearHistory={clearHistory}
                        {...props}
                    />
                </section>
            )}

            {showSearchHistory && (
                <section className="search-history__container">
                    <h2 className="search-history__heading">
                        <svg className="svg-icon">
                            <title>{searchHistoryLabel}</title>
                            <use href="#search" />
                        </svg>
                        {searchHistoryLabel}
                    </h2>
                    <HistorySection
                        type="search"
                        isReady={isReady}
                        data={searchHistory}
                        clearHistory={clearHistory}
                        {...props}
                    />
                </section>
            )}
        </div>
    );
}

export default function SearchHistory(props) {
    const { storageAdapter } = props;

    const storageService = new StorageService({ storageAdapter });

    return (
        <StoreProvider storageService={storageService}>
            <History {...props} />
        </StoreProvider>
    );
}
