/**
 * An storage service that provides methods to store and retrieve key/value data.
 */
export class StorageService {
    constructor(dto) {
        if (!dto) {
            throw new Error(
                `dto is required when instantiating a new StorageService object`
            );
        }

        [`storageAdapter`].forEach((name) => {
            if (!dto[name]) {
                throw new Error(
                    `${name} property is required when instantiating a new StorageService object`
                );
            }
        });

        this.storageAdapter = dto.storageAdapter;
    }

    /**
     * Delete a specific key.
     * @param {string} key The key.
     * @returns {void}
     */
    delete(key) {
        return this.storageAdapter.delete(key);
    }

    /**
     * Delete all values.
     * @returns {void}
     */
    deleteAll() {
        return this.storageAdapter.deleteAll();
    }

    /**
     * Get a specific value.
     * @param {string} key The key.
     * @returns {any}
     */
    get(key) {
        return this.storageAdapter.get(key);
    }

    /**
     * Get all values.
     * @returns {object}
     */
    getAll() {
        return this.storageAdapter.getAll();
    }

    /**
     * Set a specific value.
     * @param {string} key The key.
     * @returns {any}
     */
    set(key, value) {
        return this.storageAdapter.set(key, value);
    }

    /**
     * Set all values.
     * @returns {object}
     */
    setAll(values) {
        return this.storageAdapter.setAll(values);
    }
}
