import React, {
    createContext,
    useState,
    useEffect,
    useContext,
    useCallback,
} from 'react';

const Store = createContext({
    isReady: false,
    clickHistory: [],
    searchHistory: [],
    clearHistory: () => {},
    reloadHistory: () => {},
});

export function useSearchHistory() {
    const context = useContext(Store);
    return context;
}

export const StoreProvider = ({ children, storageService }) => {
    const [isReady, setIsReady] = useState(false);
    const [clickHistory, setClickHistory] = useState([]);
    const [searchHistory, setSearchHistory] = useState([]);

    // When mounted fetch data from local storage
    useEffect(() => {
        const result = storageService.getAll();

        const _clickHistory = result.clickHistory || [];
        const _searchHistory = result.searchHistory || [];

        setClickHistory(_clickHistory);
        setSearchHistory(_searchHistory);

        setIsReady(true);
    }, []);

    // Clearing local storage.
    const clearHistory = useCallback((type) => {
        const result = storageService.getAll();

        const _clickHistory = type === 'click' ? [] : result.clickHistory;
        const _searchHistory = type === 'search' ? [] : result.searchHistory;

        // Call service to update storage.
        storageService.setAll({
            clickHistory: _clickHistory,
            searchHistory: _searchHistory,
        });

        // Update state with fresh data.
        setClickHistory(_clickHistory);
        setSearchHistory(_searchHistory);
        setIsReady(true);
    }, []);

    return (
        <Store.Provider
            value={{
                clickHistory,
                clearHistory,
                searchHistory,
                isReady,
            }}
        >
            {children}
        </Store.Provider>
    );
};
